<template>
	<view class="detail">
		<view class="fixbg" :style="{'background-image':'url('+sonDetail.al.picUrl+')'}"></view>
		<musichead class="nav" :title="sonDetail.name" icon="true" color="white"></musichead>
		<view class="container" v-show="!isLoading">
			<scroll-view scroll-y="true"@scroll="scroll" :scroll-top="scrollTop">
				<view class="detail-play" @tap="handleToPlay">
					<image :src="sonDetail.al.picUrl" :class="{'detail-play-run':isPlayRotate}" mode=""></image>
					<text class="iconfont" :class="iconplay"></text>
					<view class=""></view>
				</view>
				
				<!-- 歌词 -->
				<view class="detail-lyric">
					<view class="detail-lyric-wrap" :style="{transform:'translateY('+ -(lyricIndex-1)*82 +'rpx)'}">
						<view class="detail-lyric-item" :class="{active:lyricIndex == index}" v-for="(item,index) in songLyric" :key="index">{{item.lyric}}</view>
						<!-- <view class="detail-lyric-item active">文件即可饥渴</view>
						<view class="detail-lyric-item">文件即可饥渴</view> -->
					</view>
				</view>
				<!-- 多少人喜欢 -->
				<view class="detail-like">
					<view class="detail-like-head">喜欢这首歌的人也听</view>
					
					<view class="detail-like-item" v-for="(item,index) in songSimi" 
					:key="index" @tap="handleToSimi(item.id)">
						<view class="detail-like-img">
							<image :src="item.album.artist.picUrl" mode=""></image>
						</view>
						<view class="detail-like-song">
							<view class="">{{item.name}}</view>
							<view class="">
								<image v-if="item.privilege.flag > 60 && 
								item.privilege.flag" src="../static/dujia.png" mode=""></image>
								<image v-if="item.privilege.maxbr==999000" src="../static/sq.png" mode=""></image>
								{{item.album.artists[0].name}}-{{item.name}}
							</view>
						</view>
						<text class="iconfont icon-bofang"></text>
					</view>
					
				</view>
				
				<!-- 评论 -->
				<view class="detail-comment">
					<view class="detail-comment-head">精彩评论</view>
					
					<view class="detail-comment-item" v-for="(item,index) in songComment":key="index">
						<view class="detail-comment-img">
							<image :src="item.user.avatarUrl" mode=""></image>
						</view>
						<view class="detail-comment-contenr">
							<view class="detail-comment-title">
								<view class="detail-comment-name">
									<view >{{item.user.nickname}}</view>
									<view >{{item.time | formatTime}}</view>
								</view>
								<view class="detail-comment-like">{{item.likedCount | formatCount}}<text class="iconfont icon-dianzan"></text></view>
							</view>
							<view class="detail-comment-text">{{item.content}}</view>
						</view>
					</view>
				</view>
			</scroll-view>
		</view>
	</view>
</template>

<script>
	import {songDetail,songRelated,songInfo,songUrl,songLyric} from '../common/api.js'
	import musichead from'../components/musichead/musichead.vue'
	export default {
		data() {
			return {
				sonDetail:{
					al:{	
						picUrl:''
					}
				},
				songSimi:'',
				songComment:[],
				songLyric:[],
				lyricIndex:0,
				iconplay:'icon-bofang1',
				isPlayRotate:true,
				isLoading:true,
				scrollTop:0,
				optionsID:''
			}
		},
		components:{
			musichead
		},
		onLoad(options){
			const app =getApp()
			console.log(app.a)
			// app.bgAudioManager.src='./kk.mp3'
			this.optionsID=options.id
			this.getMusic(options.id)
		
		},
		onUnload(){
			this.cancelLyricIndex()
			// #ifdef H5
				this.bgAudioManager.destroy()
			// #endif
		},
		onHide() {
			this.cancelLyricIndex()
			// #ifdef H5
				this.bgAudioManager.destroy()
			// #endif
		},
		methods: {
			getMusic(id){
				console.log(id)
			// this.$store.commit('INIT_TOPLISTTDS',res[1].data.playlist.trackIds)
				this.$store.commit('NEXT_ID',id)
				// console.log(this.$store.state.nextId)
				// console.log(this.$store.state.topListId)
				uni.showLoading({
					title:'加载中...'
				})
				
				this.isLoading=true;
				Promise.all([ songDetail(id),songRelated(id),songInfo(id),songLyric(id),songUrl(id) ]).then(res=>{
					// console.log(res[0][1].data.songs[0])
					if(res[0][1].data.code=='200'){
						this.sonDetail=res[0][1].data.songs[0]
						// console.log(this.sonDetail)
					}
					if(res[1][1].data.code=='200'){
						// console.log(res)
						this.songSimi =res[1][1].data.songs
						// console.log(this.songSimi)
					}
					if(res[2][1].data.code=='200'){
						// console.log(res)
						this.songComment =res[2][1].data.hotComments
					}
					if(res[3][1].data.code=='200'){
						let lyric =res[3][1].data.lrc.lyric
						// console.log(lyric)
						let result = []
						let re = /\[([^\]]+)\]([^\[]+)/g;
						// console.log(lyric.match(re))
						lyric.replace(re,($0,$1,$2)=>{
							result.push({"time":this.formatTime($1),"lyric":$2})
						})
						// console.log(result)
						this.songLyric=result
					}
					if(res[4][1].data.code=='200'){
						// #ifdef MP-WEIXIN
						this.bgAudioManager = uni.getBackgroundAudioManager();
						this.bgAudioManager.title=this.sonDetail.name
						// #endif
						// #ifdef H5
						if(!this.bgAudioManager){
							this.bgAudioManager=uni.createInnerAudioContext()
							this.bgAudioManager.autoplay = true;
						}
						this.iconplay='icon-bofang1'
						this.isPlayRotate=false
						// #endif
						// 音乐播放
						
						this.bgAudioManager.src = res[4][1].data.data[0].url;
						// 歌词滚动事件
						this.listenLyricIndex()
						// 音乐播放
						this.bgAudioManager.onPlay(()=>{
							this.iconplay="icon-zanting"
							this.isPlayRotate=true
							this.listenLyricIndex()
						});
						// 音乐暂停
						this.bgAudioManager.onPause(()=>{
							this.iconplay="icon-bofang1"
							this.isPlayRotate=false
							this.cancelLyricIndex()
						})
						this.bgAudioManager.onEnded(()=>{
							if(this.$store.state.nextId !== ''){
								this.getMusic(this.$store.state.nextId)
								this.bgAudioManager.play()
							}else{
								this.getMusic(this.optionsID)
								this.bgAudioManager.play()
							}
						})
					}
					this.isLoading=false
					uni.hideLoading()
				})
				
			},
			// 事件
			formatTime(value){
				let arr = value.split(':')
				return (Number(arr[0])* 60 + Number(arr[1])).toFixed(1) 
			},
			handleToPlay(){
				if(this.bgAudioManager.paused){
					this.bgAudioManager.play()
				}else{
					this.bgAudioManager.pause()
				}
			},
			// 歌词滚动
			listenLyricIndex(){
				clearInterval(this.timer)
				this.timer=setInterval(()=>{
					for(var i=0;i<this.songLyric.length;i++){
						if(this.bgAudioManager.currentTime>this.songLyric[this.songLyric.length-1].time){
							this.lyricIndex=this.songLyric.length-1
							break
						}
						if(this.bgAudioManager.currentTime>this.songLyric[i].time&&this.bgAudioManager.currentTime<this.songLyric[i+1].time){
							this.lyricIndex=i
						}
					}
					// console.log(this.lyricIndex)
				},300)
			},
			// 退出/暂停清楚定时器
			cancelLyricIndex(){
				clearInterval(this.timer)
			},
			// 点击相关歌曲，更新数据
			handleToSimi(id){
				this.scrollTop=0
				this.$nextTick(() => {
					this.getMusic(id)
				});
				
			},
			scroll(e){
				 this.scrollTop = e.detail.scrollTop
				 // console.log(this.scrollTop)
			}
		}
	}
</script>

<style scoped>
.detail-play{width: 580rpx;height: 580rpx;background-image: url(../static/disc.png);background-size: cover;margin:214rpx auto 0;position: relative;}
.detail-play image{width: 370rpx;height: 370rpx;border-radius: 50%;position: absolute;left: 0;top: 0;right: 0;bottom: 0;margin: auto; animation: 10s linear move infinite;animation-play-state: paused;}
.detail-play .detail-play-run{animation-play-state: running;}
@keyframes move{
	from{transform: rotate(0deg);}
	to{transform: rotate(360deg);}
}

.detail-play text{width: 100rpx;height: 100rpx;font-size: 100rpx;
color: white;position: absolute;left: 0;top: 0;right: 0;bottom: 0;margin: auto;}
.detail-play view{width: 230rpx;height: 360rpx;background-image: url(../static/needle.png);
position: absolute;top: -220rpx;left: 0;right: -40rpx;margin: auto;background-size: cover;}

.detail-lyric{font-size: 32rpx;line-height: 82rpx;height: 246rpx;text-align: center;overflow: hidden;color: #6f6e73;}
.detail-lyric-wrap{transition: 0.5s;}
.detail-lyric-item{height: 82rpx;}
.detail-lyric-item.active{color: white;}

.detail-like{margin: 0 30rpx;}
.detail-like-head{font-size: 36rpx;color: white;margin: 50rpx 0;}
.detail-like-item{display: flex;align-items: center;margin-bottom: 28rpx;}
.detail-like-img{width: 82rpx;height: 82rpx;border-radius: 20rpx;overflow: hidden;margin-right: 20rpx;}
.detail-like-img image{width: 100%;height: 100%;}
.detail-like-song{flex: 1;color: #c6c2bf;}
.detail-like-song view:nth-child(1){font-size: 28rpx;color: white;margin-bottom: 12rpx;}
.detail-like-song view:nth-child(2){font-size: 22rpx;}
.detail-like-song image{width: 26rpx;height: 20rpx;margin-right: 10rpx;}
.detail-like-item text{font-size: 50rpx;color: #C6C2BF;}

.detail-comment{margin: 0 30rpx;}
.detail-comment-head{font-size: 36rpx;color: white;margin: 50rpx 0;}
.detail-comment-item{margin-bottom: 28rpx;}
.detail-comment-img{width: 64rpx;height: 64rpx;border-radius: 50%;overflow: hidden;margin-right: 18rpx;}
.detail-comment-img image{width: 100%;height: 100%;}
.detail-comment-contenr{flex: 1;color: #cbcacf;}
.detail-comment-title{display: flex;justify-content: space-between;}
/* .detail-comment-name{} */
.detail-comment-name view:nth-child(1){font-size: 26rpx;}
.detail-comment-name view:nth-child(2){font-size: 20rpx;}
.detail-comment-like{font-size: 28rpx;}
.detail-comment-text{font-size: 28rpx;line-height: 40rpx;color: white;margin-top: 20rpx;border-bottom: 1px #e0e0e0 solid;padding-bottom: 40rpx;}
</style>
